export function parseCubeResultToObjects(data: {
  [key: string]: any;
}): object {
  const keys = Object.keys(data);
  const cubes = keys.map(k => k.split('.')[0]);
  const uniqueCubes = Array.from(new Set<string>(cubes));
  const objects: { [key: string]: { [key: string]: any } } = {};

  uniqueCubes.forEach(cube => {
    const applicableKeys = keys
      .filter(k => k.split('.').length > 1)
      .filter(k => k.split('.')[0] === cube);

    if (applicableKeys.length === 0) {
      return;
    }

    objects[cube] = {};
    applicableKeys.forEach(k => {
      const keys = k.split('.');

      if (keys.length === 1) {
        return;
      }

      objects[cube][keys[1]] = data[k];
    });
  });

  return objects;
}
